@import url('https://fonts.googleapis.com/css2?family=Atkinson+Hyperlegible:ital,wght@0,400;0,700;1,400;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Atkinson+Hyperlegible:ital,wght@0,400;0,700;1,400;1,700&family=Bebas+Neue&display=swap');
main{
    display:flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
#movies{
    font-family: 'Atkinson Hyperlegible', sans-serif;
    color: white;
    width:200px;
    height: 300px;
    margin: 10px;
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    transition: all 200ms ease;
    cursor: pointer;
    padding:10px;
}


#movies:hover{
    transform: scale(1.08);
    background: linear-gradient(to right top, #cc2b5e, #bc2a6d, #a82e7a, #903483, #753a88);;
}

#movies span{
    /* background-color: rgb(19, 18, 18); */
    /* height: 100%; */
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    padding: -10px;
   transform: translateY(260px);
    color: white;
    transition: all 200ms ease;

}

#movies img{
    height: 100%;
    width: 100%;
    border-radius: 6px;
}
#movies span h4{
    margin-top: 10px;
}