body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  overflow-x: hidden;
  font-family: 'Atkinson Hyperlegible', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

main{
    display:flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
#movies{
    font-family: 'Atkinson Hyperlegible', sans-serif;
    color: white;
    width:200px;
    height: 300px;
    margin: 10px;
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    transition: all 200ms ease;
    cursor: pointer;
    padding:10px;
}


#movies:hover{
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
    background: linear-gradient(to right top, #cc2b5e, #bc2a6d, #a82e7a, #903483, #753a88);;
}

#movies span{
    /* background-color: rgb(19, 18, 18); */
    /* height: 100%; */
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    padding: -10px;
   -webkit-transform: translateY(260px);
           transform: translateY(260px);
    color: white;
    transition: all 200ms ease;

}

#movies img{
    height: 100%;
    width: 100%;
    border-radius: 6px;
}
#movies span h4{
    margin-top: 10px;
}
body{
    color: white;

}


#info{
    height: 100%;
}
#info img{
    float: left;
    height: 500px;
    width: 300px;
    margin: 10px;
    border-radius: 6px;
}

#genres{
    display: flex;
    width: -webkit-min-content;
    width: min-content;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}
#genres p{
    color: black;
    background-color: yellow;
    padding: 10px;
    margin: 10px;
    border-radius: 8px;
}
h1{
    
}

#title{
    font-family: 'Bebas Neue', cursive;
    font-size: 40px;
    cursor: pointer;
}
nav{
    background-color: transparent;
    display: flex;
    width: 90vw;
    background:linear-gradient(to right top, #cc2b5e, #bc2a6d, #a82e7a, #903483, #753a88);;
    align-items: center;
    border-radius: 50px;
    height: 60px;
    margin: 10px auto 10px auto;
    justify-content: space-evenly;
}
#search{
    /* background-color: blue; */
    display: flex;
    align-items: center;
    justify-content: center;
}
#search input{
    margin: 5px;    
    border-radius: 50px;
    border: 0;
    outline: none;
    padding-left:10px ;
    height: 30px;
}

#search button{
    height: 36px;
    width: 36px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0;
        background:linear-gradient(to right top, #cc2b5e, #bc2a6d, #a82e7a, #903483, #753a88);;

    outline: none;
    border-radius:50px ;
}


